import { useEffect, useState } from "react";
import {
  packList,
  getRetailersForASMZSM,
  pacDetails,
  AsignPackageToRetailer,
  DistributorPurchasePackageList,
} from "../../../../api/login/Login";
import { Link } from "react-router-dom";
import "./packageList.css";
import { baseUrlImage } from "../../../../baseUrl";
import AccountBalanceIcon from "@mui/icons-material/AccountBalance";
import { MdVerified } from "react-icons/md";
import LockIcon from "@mui/icons-material/Lock";
import CurrencyRupeeIcon from "@mui/icons-material/CurrencyRupee";

import React from "react";
import {
  Box,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Container,
  Grid,
  Typography,
  Button,
  Divider,
  Paper,
  useTheme,
  useMediaQuery,
  Avatar,
  Autocomplete,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  InputAdornment,
} from "@mui/material";
import {
  Paid as PaidIcon,
  CheckCircle as CheckCircleIcon,
  Diamond as DiamondIcon,
  CurrencyRupee as CurrencyIcon,
} from "@mui/icons-material";
import { genrateUrl } from "../../../../utils/genrateUrl";
import Loader from "../../../../common/loader/Loader";
import toastMessage from "../../../../utils/toastMessage";
import { SpaceContextProvider } from "antd/es/space/context";

const AllowUser = ({ permission, children }) => {
  const userType = localStorage.getItem("userType");

  if (permission.includes(userType)) {
    return children;
  }

  return null;
};

const PackageCard = ({ pkg }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const primaryPrice = pkg.prices[0];
  const [popupOpen, setpopupOpen] = useState(false);
  const [packageid, setPackageId] = useState(null);

  return (
    // {loading && <Loader/>}
    <Card
      elevation={0}
      sx={{
        height: "100%",
        display: "flex",
        flexDirection: "column",
        borderRadius: 3,
        border: "1px solid var(--bs-border)",
        borderTop: "4px solid var(--bs-nav)",
        transition: "all 0.3s ease",
        "&:hover": {
          transform: "translateY(-6px)",
          boxShadow: "0 10px 25px rgba(0,0,0,0.08)",
        },
      }}
    >
      <Box sx={{ position: "relative", p: 2 }}>
        {pkg.icon_img && (
          <Box
            sx={{
              height: 80,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <CardMedia
              component="img"
              width="14px"
              image={`${genrateUrl(pkg.icon_img)}`}
              alt={pkg.package_name}
              sx={{ maxHeight: 60, objectFit: "contain" }}
            />
          </Box>
        )}
        <Box
          sx={{
            position: "absolute",
            top: 16,
            right: 16,
            color: "var(--bs-primary2)",
          }}
        >
          {
            pkg.isPurchased && (
              // <Chip
              //   icon={<CheckCircleIcon />}
              //   label="Purchased"
              //   color="success"
              //   size="small"
              // />
              <Box
                sx={{
                  position: "absolute",
                  top: 12,
                  right: 12,
                  display: "flex",
                  alignItems: "center",
                  gap: 0.5,
                  px: 1,
                  py: 0.3,
                  bgcolor: "rgba(0,200,83,0.1)",
                  borderRadius: 2,
                  color: "#00c853",
                  fontSize: 12,
                  fontWeight: 600,
                }}
              >
                <MdVerified size={14} />
                Purchased
              </Box>
            )
            //  (
            //   <Chip
            //     icon={pkg.isPaid ? <PaidIcon /> : <DiamondIcon />}
            //     label={pkg.isPaid ? "Paid" : "Free"}
            //     color={pkg.isPaid ? "primary" : "success"}
            //     size="small"
            //   />
            // )
          }
        </Box>
      </Box>

      <CardContent sx={{ flexGrow: 1, pt: 0, pt: 0 }}>
        <Typography
          variant="h6"
          fontWeight={600}
          color="var(--bs-primary2)"
          sx={{ mb: 1 }}
        >
          {pkg.package_name}
        </Typography>

        {pkg.description && (
          <Typography
            variant="body2"
            dangerouslySetInnerHTML={{ __html: pkg.description }}
            color="text.secondary"
            sx={{
              mb: 2,
              lineHeight: 1.6,
              display: "-webkit-box",
              WebkitLineClamp: 3,
              WebkitBoxOrient: "vertical",
              overflow: "hidden",
            }}
          />
        )}
      </CardContent>

      <Box
        sx={{
          p: 2,
          pt: 0,
          display: "flex",
          flexDirection: isMobile ? "column" : "row",
          gap: 1.5,
        }}
      >
        <AllowUser permission={["Distributor", "Super Distributor"]}>
          {pkg?.isPurchased === true && (
            <Button
              fullWidth
              variant="contained"
              size="small"
              sx={{
                bgcolor: "var(--bs-nav)",
                color: "var(--bs-primary2)",
                fontWeight: 600,
                "&:hover": {
                  bgcolor: "var(--bs-nav-dark)",
                },
              }}
              onClick={() => {
                setpopupOpen(true);
                setPackageId(pkg?._id);
              }}
            >
              Assign Package
            </Button>
          )}
        </AllowUser>

        <Button
          fullWidth
          variant="outlined"
          component={Link}
          to={`/package-details/${pkg?._id}`}
          size="small"
          sx={{
            borderColor: "var(--bs-nav)",
            color: "var(--bs-nav)",
            fontWeight: 600,
            "&:hover": {
              bgcolor: "rgba(0,0,0,0.04)",
            },
          }}
        >
          View Details
        </Button>
      </Box>
      {popupOpen && (
        <PackageAssigntoRetailer
          packageId={packageid}
          popupOpen={popupOpen}
          setpopupOpen={setpopupOpen}
        />
      )}
    </Card>
  );
};

const PackageList = ({ data }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  const hasPurchased = data?.some((pkg) => pkg.isPurchased);

  return (
    <>
      <Box
        sx={{
          minHeight: "100vh",
          background: "linear-gradient(135deg, #f9f9f9, #eaeaea)",
          py: 6,
        }}
      >
        <Container maxWidth="lg">
          <Box textAlign="center" mb={6}>
            <Typography
              variant={isMobile ? "h5" : "h4"}
              fontWeight={800}
              letterSpacing={1.5}
              sx={{
                textTransform: "uppercase",
                color: "var(--bs-primary2)",
                background: "linear-gradient(90deg, #ff8a00, #e52e71)",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent",
              }}
            >
              Available Packages
            </Typography>

            <Divider
              sx={{
                mt: 2,
                mx: "auto",
                width: 100,
                height: 4,
                borderRadius: 2,
                bgcolor: "var(--bs-primary2)",
              }}
            />

            <Typography
              variant="body1"
              sx={{
                mt: 2,
                color: "var(--bs-primary2)",
                fontSize: isMobile ? "0.9rem" : "1rem",
              }}
            >
              Choose the plan that fits your business â€” flexible, fast, and
              powerful.
            </Typography>
          </Box>

          {/* Package Cards */}
          <Grid container spacing={4} justifyContent="center">
            {data?.map((pkg) => (
              <Grid
                item
                key={pkg._id}
                xs={12}
                sm={6}
                md={4}
                sx={{
                  transition: "transform 0.3s ease, box-shadow 0.3s ease",
                  "&:hover": {
                    transform: "translateY(-10px)",
                  },
                }}
              >
                <PackageCard pkg={pkg} />
              </Grid>
            ))}
          </Grid>

          {/* Purchased Info */}
          {hasPurchased && (
            <Paper
              elevation={6}
              sx={{
                mt: 6,
                p: 3,
                borderRadius: 3,
                bgcolor: "rgba(76, 175, 80, 0.08)",
                border: "1px solid rgba(76, 175, 80, 0.3)",
                color: "var(--bs-primary2)",
              }}
            >
              <Box display="flex" alignItems="center">
                <CheckCircleIcon
                  sx={{
                    mr: 2,
                    fontSize: 40,
                    color: "var(--bs-primary2)",
                    filter: "drop-shadow(0px 0px 4px rgba(76,175,80,0.4))",
                  }}
                />
                <Box>
                  <Typography variant="h6" fontWeight="600" color="#111">
                    You have active subscriptions
                  </Typography>
                  <Typography variant="body2" color="var(--bs-primary2)">
                    Manage your packages anytime from your account settings.
                  </Typography>
                </Box>
              </Box>
            </Paper>
          )}
        </Container>
      </Box>
    </>
  );
};

const PackageAssigntoRetailer = ({ packageId, popupOpen, setpopupOpen }) => {
  const handleClosePopup = () => {
    setpopupOpen(false);
    setSelectedPackage(null);
    setSelectedRetailer(null);
    setBal("");
    setPin("");
  };
  const [pins, setPin] = useState("");
  const [bal, setBal] = useState("");
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [packageList, setPackageList] = useState([]);
  const [selectedPackage, setSelectedPackage] = useState(null);

  const getRetailers = async () => {
    try {
      let res = await getRetailersForASMZSM();
      if (res?.data?.error === false) {
        setRetailers(res.data?.data || []);
      } else {
        setRetailers([]);
      }
    } catch (err) {
      console.error(err);
      setRetailers([]);
    }
  };

  const getPackage = async () => {
    try {
      const res = await DistributorPurchasePackageList(packageId);
      setPackageList(res?.data?.data || []);
    } catch (error) {}
  };

  useEffect(() => {
    getRetailers();

    getPackage();
  }, []);

  const [loading, setLoading] = useState(false);

  const assignPackage = async () => {
    if (
      !selectedRetailer?._id ||
      !selectedPackage?.price_info?._id ||
      !bal?.trim()
    ) {
      alert("All fields are required");
      return;
    }

    setLoading(true);
    let body = {
      package_id: packageId,
      tpin: pins,
      price: selectedPackage?.price_info?._id,
      merchant_id: selectedRetailer?._id,
      merchantPurchasedAmt: bal || 0,
    };
    try {
      let res = await AsignPackageToRetailer(body);
      if (res?.data?.statusCode === "200") {
        handleClosePopup();
      }
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <Dialog
        open={popupOpen}
        onClose={() => setpopupOpen(false)}
        PaperProps={{
          sx: () => ({
            borderRadius: 4,
            p: 2,
            backdropFilter: "blur(10px)",
            background: "white",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 380,
            //  overflow: tabs?.length === 0 ? 'hidden' : 'auto',
          }),
        }}
      >
        <DialogTitle
          sx={{
            color: "var(--bs-primary2)",
            fontWeight: 600,
            textAlign: "center",
            fontSize: "1.0rem",
          }}
        >
          Assign Package to Retailer
        </DialogTitle>
        {/*     
                   <DialogContent sx={{ textAlign: 'center' }}>
                    
                 
                      </DialogContent> */}
        <div className="form-group col-md-12">
          {/* <label>Select Retailer</label> */}
          <Autocomplete
            options={retailers}
            getOptionLabel={(option) => option.name || ""}
            value={selectedRetailer ?? null}
            size="small"
            onChange={(e, newVal) => setSelectedRetailer(newVal)}
            renderOption={(props, option) => (
              <li {...props} key={option.retailer_id}>
                <Box sx={{ fontSize: "13px", color: "var(--bs-primary2)" }}>
                  {option.name}
                </Box>
              </li>
            )}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Select Retailer"
                InputLabelProps={{
                  sx: {
                    "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                      top: "-3px",
                    },
                  },
                }}
                sx={{
                  "& label.Mui-focused": {
                    color: "var(--bs-primary2)",
                  },
                  "& .MuiInputBase-root": { backgroundColor: "#fff" },
                  "& .MuiOutlinedInput-root": {
                    "&:hover fieldset": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused fieldset": {
                      borderColor: "var(--bs-primary1)",
                    },
                  },
                }}
              />
            )}
          />
        </div>
        <div className="form-group col-md-12">
          {/* <label>Select Package</label> */}
          <Autocomplete
            options={packageList}
            getOptionLabel={(option) =>
              `${option?.single_unit_amt} Rs , ${option?.price_info.duration}${option?.price_info?.duration_type}, ${option?.count} Count` ||
              ""
            }
            value={selectedPackage ?? null}
            size="small"
            onChange={(e, newVal) => setSelectedPackage(newVal)}
            renderOption={(props, option) => (
              <li {...props} key={option._id}>
                <Box sx={{ fontSize: "13px", color: "var(--bs-primary2)" }}>
                  {`${option?.single_unit_amt} Rs , ${option?.price_info.duration}${option?.price_info?.duration_type}, ${option?.count} Count` ||
                    ""}
                </Box>
              </li>
            )}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Select Package"
                InputLabelProps={{
                  sx: {
                    "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                      top: "-3px",
                    },
                  },
                }}
                sx={{
                  "& label.Mui-focused": {
                    color: "var(--bs-primary2)",
                  },
                  "& .MuiInputBase-root": { backgroundColor: "#fff" },
                  "& .MuiOutlinedInput-root": {
                    "&:hover fieldset": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused fieldset": {
                      borderColor: "var(--bs-primary1)",
                    },
                  },
                }}
              />
            )}
          />
        </div>

        <div className="form-group col-md-12">
          <TextField
            label="Price"
            type="text"
            name="price"
            value={bal || ""}
            onChange={(e) => {
              const value = e.target.value;
              const pattern = /^(?:\d{0,4}(?:\.\d{0,2})?)?$/;

              if (pattern.test(value)) {
                setBal(value);
              }
            }}
            sx={{
              "& .MuiOutlinedInput-root": {
                height: 42,
                "& .MuiOutlinedInput-input": {
                  py: 1,
                },
                "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
                "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
              },
            }}
            autoComplete="off"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <CurrencyRupeeIcon />
                </InputAdornment>
              ),
            }}
            fullWidth
          />
        </div>
        <div className="form-group col-md-12">
          <TextField
            label="TPIN"
            type="password"
            name="userpins"
            placeholder="Enter TPin"
            value={pins || ""}
            onChange={(e) => {
              const value = e.target.value;
              if (/^\d{0,4}$/.test(value)) {
                setPin(value);
              }
            }}
            sx={{
              "& .MuiOutlinedInput-root": {
                height: 42,
                "& .MuiOutlinedInput-input": {
                  py: 1,
                },
                "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
                "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
              },
            }}
            autoComplete="new-tpin"
            inputProps={{ inputMode: "numeric", maxLength: 4 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon />
                </InputAdornment>
              ),
            }}
            // sx={{ mt: 2 }}
            fullWidth
          />
        </div>
        <DialogActions sx={{ justifyContent: "center", mt: 2 }}>
          <Button
            onClick={handleClosePopup}
            color="var(--bs-primary2)"
            variant="outlined"
            sx={{ borderRadius: 2, px: 4, width: "150px" }}
          >
            Cancel
          </Button>
          <Button
            onClick={() => {
              if (pins?.length < 4) {
                alert("Please enter 4 digit TPIN");
                return;
              }
              assignPackage();
            }}
            disabled={loading}
            color="var(--bs-primary2)"
            variant="contained"
            sx={{
              backgroundColor: "var(--bs-primary1)",
              borderRadius: 2,
              px: 4,
              width: "150px",
            }}
          >
            Submit
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

// Usage with your API data:
const PackagesPage = () => {
  const [data, setData] = useState(null);

  const packageList = async () => {
    try {
      const res = await packList();
      setData(res?.data);
    } catch (error) {
      console.error("Error fetching package list:", error);
      setData({ error: true, message: "Failed to fetch packages." });
    }
  };

  useEffect(() => {
    packageList();
  }, []);

  if (data?.error) {
    return (
      <Box
        sx={{
          minHeight: "80vh",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          background: "linear-gradient(135deg, #1f1c2c, #928dab)",
          p: 2,
        }}
      >
        <Paper
          elevation={10}
          sx={{
            p: 5,
            borderRadius: 4,
            maxWidth: 500,
            width: "100%",
            textAlign: "center",
            color: "var(--bs-primary2)",
            backdropFilter: "blur(10px)",
            background: "rgba(255, 255, 255, 0.1)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.3)",
            transition: "transform 0.3s ease, box-shadow 0.3s ease",
            "&:hover": {
              transform: "translateY(-5px)",
              boxShadow: "0 12px 40px rgba(0,0,0,0.5)",
            },
          }}
        >
          <Typography
            variant="h4"
            sx={{
              fontWeight: "700",
              mb: 2,
              textTransform: "uppercase",
              letterSpacing: 1,
              background: "linear-gradient(90deg, #ff8a00, #e52e71)",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
            }}
          >
            Oops! Something went wrong
          </Typography>

          <Typography
            variant="body1"
            sx={{
              fontSize: "1.1rem",
              opacity: 0.9,
              mb: 4,
            }}
          >
            {data?.message || "Unable to load packages at the moment."}
          </Typography>

          <Button
            variant="contained"
            color="var(--bs-primary2)"
            size="large"
            onClick={packageList}
            sx={{
              borderRadius: 3,
              px: 4,
              fontWeight: "600",
              textTransform: "none",
              boxShadow: "0 4px 12px rgba(255, 138, 0, 0.4)",
              "&:hover": {
                background: "linear-gradient(90deg, #e52e71, #ff8a00)",
                boxShadow: "0 8px 20px rgba(229, 46, 113, 0.6)",
              },
            }}
          >
            Try Again
          </Button>
        </Paper>
      </Box>
    );
  }

  return <PackageList data={data?.data || []} />;
};
export default PackagesPage;
